<script type="text/typescript">;
export let store;
export let name;
export let onChange = null;
let value;
store.subscribe((v) => (value = v[name]));
const ourChange = (e) => {
    store.update(v => {
        v[name] = value;
        return v;
    });
    if (onChange) {
        onChange(e);
    }
};
</script>

<style>
  label {
    margin: 0.25rem;
    position: relative;
  }

  select {
    padding: 0.25rem;
    bottom: 0;
    position: absolute;
  }
</style>

<label>
  <select bind:value on:blur={ourChange} {name} {...$$restProps}>
    <slot />
  </select>
</label>
